import React from 'react';
import { Row, Col, Card, Form, Button, InputGroup, FormControl, DropdownButton, Dropdown, Table } from 'react-bootstrap';

import BootstrapSwitchButton from 'bootstrap-switch-button-react'
import Aux from "../../hoc/_Aux";

import DatePicker from 'react-datepicker';
import AsyncSelect from 'react-select/async';


import ErrorBody from '../../App/components/Reusables/ErrorBody';
import Jhilikka from '../../App/components/Reusables/Jhilikka';
import Loader from '../../App/components/Reusables/Loader'
import { getLedgerEntry, updateLedgerEntry } from '../../api/accounting/ledger';
import isEmpty from '../../validation/isEmpty';

import { validateLedgerEntryUpdate } from '../../validation/ledger'

const Update = (props) => {

    const [loading, setLoading] = React.useState(true);
    const [submitting, setSubmitting] = React.useState(false);
    const [ledgerEntryData, setLedgerEntryData] = React.useState({
        id: null,
        amount: '',
        entryDate: new Date(),
    })
    const [errors, setErrors] = React.useState({});


    React.useEffect(() => {
        const controller = new AbortController();
        (async () => {
            let ladder = 0;
            getLedgerEntry({ action: 'get', ledger_entry_id: props.match.params.id }, controller.signal)
                .then(async (res) => {
                    console.log(res)
                    if (res.json.status) {
                        const ledgerEntry = res.json.ledger_entries[0];
                        await setLedgerEntryData({
                            ...ledgerEntryData,
                            id: ledgerEntry.id,
                            account: ledgerEntry.account_str,
                        })
                        setLoading(false)
                    } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
                }).catch((err) => {
                    console.log(err)
                });
            if (ladder === 1) {
                setLoading(false)
            }
        })()
        return () => controller.abort()
    }, []);

    const _entryDateChange = (selectedDate) => {
        setErrors({ ...errors, entryDate: '' })
        setLedgerEntryData({ ...ledgerEntryData, entryDate: selectedDate })
    }





    const _ledgerEntryDataChange = (e) => {
        console.log(e.target.value);
        const type = e.target.type;
        const name = e.target.name;
        const value = e.target.value;
        setErrors({ ...errors, [name]: '' })

        setLedgerEntryData({ ...ledgerEntryData, [name]: type === 'number' ? parseFloat(value) : value })
    }

    const _isAddChange = (isAdd) => {
        setLedgerEntryData({ ...ledgerEntryData, isAdd: isAdd });
    }

    const _update = async () => {
        //check if there are errors(in component validation)
        const { isValid, errors } = validateLedgerEntryUpdate(ledgerEntryData)
        if (!isValid) {
            console.log(errors)
            setErrors({ ...errors })
            return;
        }
        setSubmitting(true);
        const requestJson = {
            "action": "add",
            "ledger_entry_id": props.match.params.id,
            "amount": ledgerEntryData.amount,
            "date": ledgerEntryData.entryDate,
        }

        console.log(requestJson);

        updateLedgerEntry(requestJson)
            .then((res) => {
                console.log(res);
                if (res.json.status) {
                    Jhilikka.fire({ icon: 'success', title: 'Ledger Entry updated successfully!', background: '#dffff0' })
                    props.history.push('/ledger-entry/' + props.match.params.id)
                } else {
                    setSubmitting(false)
                    Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' })
                }
            }).catch((err) => {
                setSubmitting(false)
                console.log(err)
            });
    }
    // console.log(ledgerEntryData);

    if (loading) return <Loader />
    return (
        <Aux>
            <Row>
                <Col>
                    <Card>
                        <Card.Body>
                            <h5>Update Ledger Entry</h5>
                            <hr />
                            <Row>
                                <Col md={6}>

                                    <Form.Group  >
                                        <Form.Label>Date</Form.Label> <br />
                                        <DatePicker onChange={_entryDateChange} placeholder="Pick Date" name="entryDate" selected={ledgerEntryData.entryDate} />
                                        <ErrorBody>{errors.entryDate}</ErrorBody>
                                    </Form.Group>
                                </Col>

                                <Col md={6}>
                                    <Form.Group  >
                                        <Form.Label>Account</Form.Label> <br />
                                        <Form.Control
                                            readOnly={true}
                                            name="account"
                                            value={ledgerEntryData.account || ''}
                                            onChange={_ledgerEntryDataChange}
                                            type="text"
                                            placeholder="account"
                                        />
                                    </Form.Group>
                                </Col>

                                <Col md={6}>
                                    <Form.Group  >
                                        <Form.Label>Amount*</Form.Label> <br />
                                        <Form.Control
                                            name="amount"
                                            value={ledgerEntryData.amount}
                                            onChange={_ledgerEntryDataChange}
                                            type="number"
                                            placeholder="Amount to be added (eg -200 or 200)"
                                        />
                                        <ErrorBody>{errors.amount}</ErrorBody>
                                    </Form.Group>
                                </Col>

                            </Row>

                            <div className="d-flex justify-content-end" >
                                <Button
                                    variant="warning"
                                    onClick={() => { props.history.push('/ledger-entry/' + props.match.params.id) }}
                                >
                                    Cancel
                                    </Button>
                                <Button
                                    disabled={submitting}
                                    variant="primary"
                                    onClick={_update}
                                >
                                    {submitting ? <><i className="fa fa-spinner text-c-white f-15 fa-pulse m-l-1"></i>Updating</> : 'Update'}
                                </Button>
                            </div>
                        </Card.Body>
                    </Card>
                </Col>
            </Row>
        </Aux >
    );
}

export default Update;
